import React, { Component, Fragment } from 'react'
import { LanguageInterface, MatchingQuestionAnswerInterface, NumericalQuestionAnswerInterface, QuestionAnswerInterface, QuestionInterface, QuestionItemInterface, QuestionLanguages, questionTypeElementsType } from '../../../../../interfaces/question'
import { connect } from "react-redux";
import {
  Auth, 
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  loadQuestionTypes,
  System,
  PushPopupAlert,
  UpdateQuestion,
  PrepareExamsStoreInterface,
  addExamQuestion,
} from "../../../../../actions";
import { StoreState } from "../../../../../reducers";
import { PopupAlertData } from '../../../../../components/Alert/Popup';
import Courses from '../../../../../components/Courses/Courses';
import { course } from '../../../../../interfaces/course';
import Modal, { ModalSize, Themes } from '../../../../../components/Modal/Modal';
import QuestionAnswer from './QuestionAnswer';
import { MdModeEdit } from 'react-icons/md';
import HandleInputProps from './HandleInput'
import { HiCheck } from 'react-icons/hi';
import Loading from '../../../../../components/Loading/Loading';
import { LoadingType } from '../../../../../components/Loading/LoadingV2';
import QuestionChapters from './QuestionChapters';
import { PopupAlertTheme } from '../../../../../interfaces/popupAlert';
import { BiArrowBack } from 'react-icons/bi';
import { getQuestionType } from '../../../../../utils/question_bank';
import ChangeLanguage from '../../../../../components/ChangeLanguage/ChangeLanguage';

interface EditQuestionProps {
  selectedQuestion: QuestionInterface;
  questionsList: QuestionInterface[];
  questionTypes: questionTypesStoreInterface;
  system: System;
  course: courseStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
  loadCourses: (callback: (loading: boolean) => void) => void;
  unSelectQuestion: () => void;
  UpdateQuestion: (question: QuestionInterface, callback: (loading: boolean) => void, actionStatus: (loading: boolean, msg: string) => void) => void;
  prepareExam: PrepareExamsStoreInterface;
  addExamQuestion: (question: QuestionInterface) => void;
}

interface EditQuestionState {
  loading: boolean;
  id: string;
  course: string; // Course ID
  question: QuestionItemInterface[];
  type: questionTypeElementsType | "Invalid";
  chapters: string[] // CourseChapter: ID
  customNumber: number;
  marks: number;
  answer: QuestionAnswerInterface[];
  selectedLanguage: LanguageInterface;
  showFilter: boolean;
  selectedKey: SelectedKeysEdit | null;
  showQuestionChapters: boolean;
}

export enum SelectedKeysEdit {
  Title = "Title",
  Description = "Description",
  QuestionAnswer = "QuestionAnswer",
}

class _EditQuestion extends Component<EditQuestionProps, EditQuestionState> {
  constructor(props: EditQuestionProps) {
		super(props);

		this.state = {
      loading: false,
      id: this.props.selectedQuestion.id,
      course: this.props.selectedQuestion.course,
      question: this.props.selectedQuestion.question,
      type: this.props.selectedQuestion.type,
      chapters: this.props.selectedQuestion.chapters,
      customNumber: this.props.selectedQuestion.customNumber,
      marks: this.props.selectedQuestion.marks,
      answer: this.props.selectedQuestion.answer,
      selectedLanguage: QuestionLanguages[0],
      showFilter: false,
      selectedKey: null,
      showQuestionChapters: false,
		};
	}

  QuestionLang = (question: QuestionItemInterface[]) => {
    let res = question.find(itm => itm.language === this.state.selectedLanguage.key);
    return res !== undefined ? res : question[0];
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  }

  actionStatus = (status: boolean, msg: string) => {
    this.props.PushPopupAlert({
      title: status === true ? "Success!" : "Failed!",
      description: msg,
      theme: status === true ? PopupAlertTheme.success : PopupAlertTheme.danger,
    });
  }

  handleUpdateQuestionDetails = (question: QuestionItemInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language) {
        updatedQuestion.push(question);
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  updateAnswerSingleMultiple = (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language || getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.CalculatedMultipleChoice) {
        let res: QuestionAnswerInterface[] = [];
        for (let answer = 0; answer < current.answer.length; answer++) {
          if (current.answer.find(itm => itm.value === editedAnswerIndex) !== undefined) {
            if (current.answer[answer].value === editedAnswerIndex) {
              res.push(editedAnswer);
            } else {
              res.push(current.answer[answer])
            }
          }
        }
        if (getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.CalculatedMultipleChoice) {
          updatedQuestion.push({
            description: current.description,
            language: current.language,
            status: current.status,
            title: current.title,
            answer: res
          })
        } else {
          updatedQuestion.push({
            description: question.description,
            language: question.language,
            status: question.status,
            title: question.title,
            answer: res
          })
        }
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  updateAnswerSimpleCalculate = (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language || getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.SimpleCalculate) {
        if (getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.SimpleCalculate) {
          updatedQuestion.push({
            description: current.description,
            language: current.language,
            status: current.status,
            title: current.title,
            answer: [editedAnswer]
          })
        } else {
          updatedQuestion.push({
            description: question.description,
            language: question.language,
            status: question.status,
            title: question.title,
            answer: [editedAnswer]
          })
        }
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  updateAnswerNumerical = (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language || getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.Numeric) {
        if (getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.Numeric) {
          updatedQuestion.push({
            description: current.description,
            language: current.language,
            status: current.status,
            title: current.title,
            answer: [editedAnswer]
          })
        } else {
          updatedQuestion.push({
            description: question.description,
            language: question.language,
            status: question.status,
            title: question.title,
            answer: [editedAnswer]
          })
        }
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  updateAnswerNumericalValue = (question: QuestionItemInterface, answer_id: string, editedAnswer: NumericalQuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    let newEditedAnswer: NumericalQuestionAnswerInterface[] = [];
    if (editedAnswer.value.length === 1) {
      newEditedAnswer = [{
        id: answer_id,
        value: [editedAnswer.value[0], question.answer[0].value[1]],
        status: [
          editedAnswer.value[0] - question.answer[0].value[1],
          editedAnswer.value[0] + question.answer[0].value[1],
        ]
      }]
    } else if (editedAnswer.status.length === 1) {
      newEditedAnswer = [{
        id: answer_id,
        status: [
          editedAnswer.value[0] - editedAnswer.status[0],
          editedAnswer.value[0] + editedAnswer.status[0],
        ],
        value: [editedAnswer.value[0], editedAnswer.status[0]]
      }]
    } else {
      newEditedAnswer = [editedAnswer]
    }
    for (let current of this.state.question) {
      if (question.language === current.language || getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.Numeric) {
        if (getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.Numeric) {
          updatedQuestion.push({
            description: current.description,
            language: current.language,
            status: current.status,
            title: current.title,
            answer: newEditedAnswer
          })
        } else {
          updatedQuestion.push({
            description: question.description,
            language: question.language,
            status: question.status,
            title: question.title,
            answer: newEditedAnswer
          })
        }
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  updateShortAnswer = (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language) {
        updatedQuestion.push({
          description: question.description,
          language: question.language,
          status: question.status,
          title: question.title,
          answer: [editedAnswer]
        })
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  addAnswerSingleMultiple = (question: QuestionItemInterface, addedAnswer: QuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    let newFiltered: QuestionAnswerInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language) {
        newFiltered = current.answer;
        if (getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.MultipleChoice) {
          newFiltered.push(addedAnswer);
        } 
        if (getQuestionType(this.props.questionTypes.questionTypes, this.state.type) === questionTypeElementsType.SingleMultiple) {
          if (addedAnswer.status === true && current.answer.filter(itm => itm.status === true).length !== 0) {
            newFiltered.push({
              id: addedAnswer.id,
              value: addedAnswer.value,
              status: false,
            });
          } else {
            newFiltered.push(addedAnswer);
          }
        }
        updatedQuestion.push({
          description: question.description,
          language: question.language,
          status: question.status,
          title: question.title,
          answer: newFiltered
        })
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  addAnswerMatchingQuestion = (question: QuestionItemInterface, addedAnswer: MatchingQuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    let newFiltered: MatchingQuestionAnswerInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language) {
        newFiltered = current.answer;
        newFiltered.push(addedAnswer);
        updatedQuestion.push({
          description: question.description,
          language: question.language,
          status: question.status,
          title: question.title,
          answer: newFiltered
        })
      } else {
        updatedQuestion.push(current);
      }
    }
    console.log("Updated: ", updatedQuestion)
    this.setState({ question: updatedQuestion });
  }

  removeAnswerSingleMultiple = (question: QuestionItemInterface[], removed: QuestionAnswerInterface) => {
    if (window.confirm("Are you sure do ypu want to delete?") === true) {
      let updatedQuestion: QuestionItemInterface[] = [];
      for (let current of this.state.question) {
        let res: QuestionAnswerInterface[] = current.answer.filter(itm => itm.id !== removed.id);
        updatedQuestion.push({
          description: current.description,
          language: current.language,
          status: current.status,
          title: current.title,
          answer: res
        });
      }
      this.setState({ question: updatedQuestion });
    }
  }

  editSingleMultipleStatus = (answer_id: string, status: boolean) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      let res: QuestionAnswerInterface[] = [];
      for (let ans of current.answer) {
        if (ans.status === true) {
          res.push({
            id: ans.id,
            value: ans.value,
            status: false,
          });
        }else if (ans.id === answer_id) {
          res.push({
            id: ans.id,
            value: ans.value,
            status: status,
          });
        }
        else {
          res.push(ans);
        }
      }
      updatedQuestion.push({
        description: current.description,
        language: current.language,
        status: current.status,
        title: current.title,
        answer: res
      });
    }
    this.setState({ question: updatedQuestion });
  }

  editTrueFalseStatus = (answer_id: string, status: boolean) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      let res: QuestionAnswerInterface[] = [];
      for (let ans of current.answer) {
        if (ans.status === true && status === false) {
          res.push({
            id: ans.id,
            value: ans.value,
            status: true,
          });
        } else if (ans.status === true) {
          res.push({
            id: ans.id,
            value: ans.value,
            status: false,
          });
        } else if (ans.id === answer_id) {
          res.push({
            id: ans.id,
            value: ans.value,
            status: status,
          });
        }
        else {
          res.push(ans);
        }
      }
      updatedQuestion.push({
        description: current.description,
        language: current.language,
        status: current.status,
        title: current.title,
        answer: res
      });
    }
    this.setState({ question: updatedQuestion });
  }

  editMatchingStatus = (answer_id: string, status: string) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      let res: MatchingQuestionAnswerInterface[] = [];
      for (let ans of current.answer) {
        if (ans.id === answer_id) {
          res.push({
            id: ans.id,
            value: ans.value,
            status: status,
          });
        }
        else {
          res.push(ans);
        }
      }
      updatedQuestion.push({
        description: current.description,
        language: current.language,
        status: current.status,
        title: current.title,
        answer: res
      });
    }
    this.setState({ question: updatedQuestion });
  }

  editMatchingValue = (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: MatchingQuestionAnswerInterface) => {
    let updatedQuestion: QuestionItemInterface[] = [];
    for (let current of this.state.question) {
      if (question.language === current.language) {
        let res: QuestionAnswerInterface[] = [];
        for (let answer = 0; answer < current.answer.length; answer++) {
          if (current.answer.find(itm => itm.value === editedAnswerIndex) !== undefined) {
            if (current.answer[answer].value === editedAnswerIndex) {
              res.push(editedAnswer);
            } else {
              res.push(current.answer[answer])
            }
          }
        }
        updatedQuestion.push({
          description: question.description,
          language: question.language,
          status: question.status,
          title: question.title,
          answer: res
        })
      } else {
        updatedQuestion.push(current);
      }
    }
    this.setState({ question: updatedQuestion });
  }

  setAddChapter = (chapter_id: string) => {
    if (this.state.chapters.find(itm => itm === chapter_id) === undefined) {
      this.setState({ chapters: [...this.state.chapters, chapter_id] });
    } else {
      this.setState({ chapters: this.state.chapters.filter(itm => itm !== chapter_id) });
    }
  }

  submitEditedQuestion = async () => {
    if (this.state.chapters.length === 0) {
      return this.props.PushPopupAlert({
        title: "Error",
        description: "Please choose question chapters!",
        theme: PopupAlertTheme.danger,
      });
    }
    this.setState({ loading: true });
    let addedQuestion = {
      id: this.state.id,
      course: this.state.course,
      question: this.state.question,
      type: this.state.type,
      chapters: this.state.chapters,
      customNumber: this.state.customNumber,
      marks: this.state.marks,
      answer: this.state.answer,
    }
    await this.props.UpdateQuestion(addedQuestion, this.setLoading, this.actionStatus);
    // Write twice to replace current state
    this.props.addExamQuestion(addedQuestion);
    this.props.addExamQuestion(addedQuestion);
    this.props.unSelectQuestion();
  }

  componentDidMount = () => {
    if (this.props.questionTypes.questionTypes === null || this.props.questionTypes.questionTypes.length === 0) {
      this.props.loadQuestionTypes(this.setLoading);
    }

    if (this.props.course.courses === null || this.props.course.courses.length === 0) {
      this.props.loadCourses(this.setLoading);
    }
  }

  setSelectedCourse = (course: course | null) => {
    if (course !== null) {
      this.setState({ course: course.course_id });
      this.setState({ showFilter: false });
      this.setState({ chapters: [] });
      this.setState({ showQuestionChapters: true });
    }
  }

  setSelectedKey = (selectedKey: SelectedKeysEdit | null) => {
    this.setState({ selectedKey: selectedKey });
  }

  updateMarks = (marks: number) => {
    if (marks) {
      this.setState({ marks: marks });
    }
  }

  setSelectedLang = (lang: LanguageInterface) => {
    this.setState({ selectedLanguage: lang })
  }

  render() {
    return (
      <Fragment>
        {this.state.selectedKey !== SelectedKeysEdit.QuestionAnswer && 
        <ChangeLanguage 
        setSelectedLanguage={this.setSelectedLang} 
        selectedLanguage={this.state.selectedLanguage} />}
        {this.state.loading === true ? 
        <div className="bg-white rounded px-4 py-5 mt-4">
          <Loading type={LoadingType.white} /> 
        </div>
        :
        <div className="bg-gray-100 shadow-md pb-2 rounded animate__animated animate__fadeIn animate__fast mb-44">
          <div className="flex flex-col items-center justify-between">
            <div className="flex flex-row items-center justify-between relative bottom-0 bg-white border-t-4 border-blue-500 w-full py-2 px-2 shadow-md">
              <div className="flex flex-row items-center gap-2">
                <div onClick={() => this.props.unSelectQuestion()} className="btn cursor-pointer bg-blue-100 text-blue-600 hover:bg-blue-200 h-full flex flex-row gap-2 items-center"><BiArrowBack className="text-xl" /> Back to list</div>
                <div className="text-xl font-extrabold text-gray-600">Edit question</div>
              </div>
              <div className="flex flex-row items-center gap-2">
                {/* <div className="flex flex-row items-center justify-end">
                  {this.state.selectedKey === null && QuestionLanguages.map((item, i) => (
                    <div onClick={() => this.setState({ selectedLanguage: item })} key={i + 1}
                    className={`${this.state.selectedLanguage.key === item.key ? "text-blue-600 bg-blue-50 rounded font-bold" : "text-gray-600"} text-center text-sm px-3 py-2 cursor-pointer hover:bg-blue-50 hover:text-blue-600 hover:border-blue-600`}>
                      {item.value}
                    </div>
                  ))}
                </div> */}
                {this.state.selectedKey === null && <div onClick={() => this.submitEditedQuestion()} className="btn cursor-pointer bg-blue-600 text-white">Save changes</div>}
              </div>
            </div>
          </div>
          <form onSubmit={this.submitEditedQuestion} className="px-2 py-2">
            <div className="flex flex-row items-center justify-between border mb-3 text-sm gap-1 bg-white px-4 py-3 rounded">
              <div className="flex flex-col">
                <label className="text-gray-600 font-bold">Selected course</label>
                <span className="font-bold text-blue-600 flex flex-row items-center gap-2">
                  {/* <BsFillCaretRightFill className="text-xl" /> */}
                  {this.props.course.courses !== null && this.props.course.courses.find(itm => itm.course_id === this.state.course)?.course_code} -
                  &nbsp;{this.props.course.courses !== null && this.props.course.courses.find(itm => itm.course_id === this.state.course)?.course_name}
                </span>
              </div>
              <div className="flex flex-row items-center gap-2">
                {this.state.chapters.length === 0 ?
                <div onClick={() => this.setState({ showQuestionChapters: true })} className={`bg-yellow-100 hover:bg-yellow-700 text-yellow-700 hover:text-white rounded px-3 py-2 cursor-pointer font-bold`}>
                  Add chapters
                </div> : 
                <div onClick={() => this.setState({ showQuestionChapters: true })} className="bg-blue-50 hover:bg-blue-200 text-blue-600 hover:text-blue-800 rounded px-3 py-2 cursor-pointer font-bold">
                  Edit chapters ({this.state.chapters.length})
                </div>
                }

                <div onClick={() => this.setState({ showFilter: true })} className="bg-gray-100 text-gray-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-100 hover:text-blue-600">
                  <MdModeEdit className="text-3xl" />
                </div>
              </div>
            </div>
            <div className="flex flex-row items-center justify-between mb-3 text-sm bg-white px-4 py-3 rounded border gap-2">
              <div className="flex flex-col w-full">
                <div className="text-gray-600 font-bold flex flex-row items-center justify-between">
                  Question title
                  {this.state.selectedKey !== null && this.state.selectedKey === SelectedKeysEdit.Title &&
                  <div onClick={() => this.setState({ selectedKey: null })} className="bg-blue-100 text-blue-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-200 hover:text-blue-700">
                    <HiCheck className="text-3xl" />
                  </div>}
                </div>
                {this.state.selectedKey !== SelectedKeysEdit.Title &&
                <label className="text-blue-600 font-bold flex flex-row gap-2 items-center">
                  {/* <BsFillCaretRightFill className="text-xl" /> */}
                  {this.QuestionLang(this.state.question).title}
                </label>}
                {this.state.selectedKey !== null && this.state.selectedKey === SelectedKeysEdit.Title &&
                <HandleInputProps 
                languages={QuestionLanguages} 
                questionItems={this.state.question}
                selectedKey={this.state.selectedKey}
                handleUpdateQuestionDetails={this.handleUpdateQuestionDetails} />}
              </div>
              {this.state.selectedKey !== SelectedKeysEdit.Title &&
              <div onClick={() => this.setState({ selectedKey: SelectedKeysEdit.Title })} className="bg-gray-100 text-gray-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-100 hover:text-blue-600">
                <MdModeEdit className="text-3xl" />
              </div>}
            </div>
            <div className="flex flex-row items-center justify-between mb-3 text-sm bg-white border px-4 py-3 rounded gap-2">
              <div className="flex flex-col w-full">
                <div className="text-gray-600 font-bold flex flex-row items-center justify-between">
                  Question details
                  {this.state.selectedKey !== null && this.state.selectedKey === SelectedKeysEdit.Description &&
                  <div onClick={() => this.setState({ selectedKey: null })} className="bg-blue-100 text-blue-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-200 hover:text-blue-700">
                    <HiCheck className="text-3xl" />
                  </div>}
                </div>
                {this.state.selectedKey !== SelectedKeysEdit.Description &&
                <label className="text-blue-600 font-bold flex flex-row gap-2 items-center">
                  {/* <BsFillCaretRightFill className="text-xl" /> */}
                  {this.QuestionLang(this.state.question).description}
                </label>}
                {this.state.selectedKey !== null && this.state.selectedKey === SelectedKeysEdit.Description &&
                <HandleInputProps 
                languages={QuestionLanguages} 
                questionItems={this.state.question}
                selectedKey={this.state.selectedKey}
                handleUpdateQuestionDetails={this.handleUpdateQuestionDetails} />}
              </div>
              {this.state.selectedKey !== SelectedKeysEdit.Description &&
              <div onClick={() => this.setState({ selectedKey: SelectedKeysEdit.Description })} className="bg-gray-100 text-gray-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-100 hover:text-blue-600">
                <MdModeEdit className="text-3xl" />
              </div>}
            </div>
            
            <QuestionAnswer 
              selectedQuestionType={getQuestionType(this.props.questionTypes.questionTypes, this.state.type)} 
              selectedQuestionItem={this.QuestionLang(this.state.question)} 
              wholeQuestion={this.state.question} 
              selectedLanguage={this.state.selectedLanguage} 
              updateAnswerSingleMultiple={this.updateAnswerSingleMultiple} 
              updateAnswerSimpleCalculate={this.updateAnswerSimpleCalculate}
              updateAnswerNumerical={this.updateAnswerNumerical}
              updateAnswerNumericalValue={this.updateAnswerNumericalValue}
              updateShortAnswer={this.updateShortAnswer}
              addAnswerSingleMultiple={this.addAnswerSingleMultiple}
              addAnswerMatchingQuestion={this.addAnswerMatchingQuestion}
              removeAnswerSingleMultiple={this.removeAnswerSingleMultiple} 
              selectedKey={this.state.selectedKey} 
              setSelectedKey={this.setSelectedKey}
              editSingleMultipleStatus={this.editSingleMultipleStatus} 
              editTrueFalseStatus={this.editTrueFalseStatus}
              editMatchingValue={this.editMatchingValue}
              editMatchingStatus={this.editMatchingStatus}
              marks={this.state.marks}
              updateMarks={this.updateMarks}
              />
          </form>
        </div>}
        {this.state.showFilter === true &&
        <Modal
          title="Filter courses by school levels"
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ showFilter: false })}
          backDropClose={true}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          padding={{title: true,}}>
          <Courses setSelectedCourse={this.setSelectedCourse} selected_course_id={this.state.course} />
        </Modal>}
        {this.state.showQuestionChapters === true && 
        <Modal
          title="Question course chapters"
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ showQuestionChapters: false })}
          backDropClose={true}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          padding={{title: true,}}>
          {this.props.course.selected_course !== null &&
            <QuestionChapters 
              selectedCourse={this.props.course.courses && this.props.course.courses.find(itm => itm.course_id === this.state.course)} 
              addedChapters={this.state.chapters} 
              setAddChapter={this.setAddChapter} />}
              <div className="flex flex-row items-center justify-between mt-3 px-2 py-2">
                <div className=""></div>
                <div className="btn bg-blue-600 text-white cursor-pointer" onClick={() => this.setState({ showQuestionChapters: false })}>Done</div>
              </div>
        </Modal>}
      </Fragment>
    )
  }
}

const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  questionTypes,
  system,
  prepareExam,
}: StoreState): { auth: Auth; course: courseStoreInterface; schoolLevels: schoolLevelsStoreInterface; questionBank: QuestionBankStoreInterface; questionTypes: questionTypesStoreInterface; system: System; prepareExam: PrepareExamsStoreInterface; } => {
  return { auth: auth, course: course, schoolLevels: schoolLevels, questionBank: questionBank, questionTypes: questionTypes, system: system, prepareExam: prepareExam, };
};

const EditQuestion = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  setUploadedQuestions,
  loadQuestionTypes,
  PushPopupAlert,
  UpdateQuestion,
  addExamQuestion,
})(_EditQuestion);

export default EditQuestion;