import React, { Component, Fragment } from "react";
import Courses from "../../components/Courses/Courses";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { connect } from "react-redux";
import {
  Auth, 
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  loadQuestionTypes,
  loadQuestionBank,
  setSelectedCourse,
  PrepareExamsStoreInterface,
  addExamQuestion,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  setSelectedPeriod,
  System,
  PushPopupAlert,
} from "../../actions";
import { StoreState } from "../../reducers";
import { course } from "../../interfaces/course";
import { QuestionInterface, uploadQuestionsResponse } from "../../interfaces/question";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import Questions from "../../components/Questions/Questions";
import Loading from "../../components/Loading/Loading";
import QuestionItem from "../../components/Questions/QuestionItem";
import { ApplicationPeriodGet } from "../../interfaces/application";
import ApplicationPeriods from "../../components/ApplicationPeriods/ApplicationPeriods";
import { API } from "../../utils/api";
import axios from "axios";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import { EXAM_QUESTIONS_NUMBERING, PrepareExamStatus, QuestionNumberFormat } from "../../interfaces/prepare_exam";
import { Redirect } from "react-router-dom";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import EditQuestion from "../QuestionBank/AddQuestion/UploadQuestion/EditQuestion/EditQuestion";
import { MdAdd } from "react-icons/md";
import ReactQuill from "react-quill";
import 'react-quill/dist/quill.snow.css'; // ES6
import PrepareExamSteps, { PrepareExamStepsEnum } from "./PrepareExamSteps";


export const getHoursList = () => {
  let hrs:number[] = [];
  for (let x=0; x<=24; x++) {
    hrs = [...hrs, x]
  }
  return hrs;
}
export const getMinutesList = () => {
  let mins:number[] = [];
  for (let x=0; x<=59; x++) {
    mins = [...mins, x]
  }
  return mins;
}

interface AppPropsProps {
  selectedCourse: course;
  questionBank: QuestionBankStoreInterface;
  course: courseStoreInterface;
  questionTypes: questionTypesStoreInterface;
  prepareExam: PrepareExamsStoreInterface;
  system: System;
  setUploadingStatus: (status: boolean) => void;
  setSelectedCourse: (coure: course | null) => void;
  setUploadedQuestions: (questions: uploadQuestionsResponse | null) => void;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  loadQuestionBank: (course_id: string, callback: (loading: boolean) => void) => void;
  addExamQuestion: (question: QuestionInterface) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
interface AppPropsState {
  loading: boolean;
  selectCourse: boolean;
  selectPeriod: boolean;
  addQuestionModal: boolean;
  formErr: {
    element: string;
    msg: string;
  } | null;
  questions_per_page: number;
  exam_date: string;
  start_time: string;
  end_time: string;
  duration_hours: number;
  duration_minutes: number;
  number_format: string;
  discription: string;
  termes: string;
  redirectToExams: boolean;
  editQuestion: QuestionInterface | null;
  currentStep: PrepareExamStepsEnum;
}

class _PrepareExam extends Component<AppPropsProps, AppPropsState> {
  constructor(props: AppPropsProps) {
    super(props);

    this.state = {
      loading: false,
      selectCourse: false,
      selectPeriod: false,
      addQuestionModal: false,
      formErr: {
        element: "", msg: ""
      },
      questions_per_page: 1,
      exam_date: "",
      start_time: "",
      end_time: "",
      duration_hours: 1,
      duration_minutes: 0,
      number_format: QuestionNumberFormat.NUMBER,
      discription: "",
      termes: "",
      redirectToExams: false,
      editQuestion: null,
      currentStep: PrepareExamStepsEnum.Step1,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  }
  addQuestionToPaper = (question: QuestionInterface) => {
    this.props.addExamQuestion(question);
  }
  removeToQuestionPaper = (question: QuestionInterface) => {
    if (window.confirm("Are you sure do you want to remove question to question paper?")) {
      this.props.addExamQuestion(question);
    }
  }
  showPeriodFilter = (status: boolean) => {
    this.setState({ selectPeriod: status })
  }
  setStep = (step: PrepareExamStepsEnum) => {
    if (step === PrepareExamStepsEnum.Step2) {
      if (this.props.periods.selected_period === null || this.props.periods.selected_period.is_active === false) {
        return this.setState({
          formErr: {
            element: "period",
            msg: "Please select valid period!",
          },
          selectPeriod: true,
        })
      }
      if (this.props.course.selected_course === null) {
        return this.setState({
          formErr: {
            element: "course",
            msg: "Please select a course!",
          },
          selectCourse: true,
        })
      }
      if (this.props.prepareExam.questions.length === 0) {
        return this.setState({
          formErr: {
            element: "questions",
            msg: "Please choose questions",
          }
        })
      }
    }
    this.setState({ currentStep: step});
  }

  StepStatus = (step: PrepareExamStepsEnum) => {
    if (step === PrepareExamStepsEnum.Step1) {
      if (this.props.periods.selected_period === null || this.props.periods.selected_period.is_active === false) {
        return false;
      }
      if (this.props.course.selected_course === null) {
        return false;
      }
      if (this.props.prepareExam.questions.length === 0) {
        return false;
      }
    }
    if (step === PrepareExamStepsEnum.Step2) {
      if (this.state.exam_date === "") {
        return false;
      }
      if (this.state.start_time === "") {
        return false;
      }
      if (this.state.end_time === "") {
        return false;
      }
      if (this.state.duration_hours === 0 && this.state.duration_minutes === 0) {
        return false;
      }
      if (this.state.questions_per_page < 1) {
        return false;
      }
      if (this.state.number_format === "") {
        return false;
      }
      if (this.state.discription === "") {
        return false;
      }
      if (this.state.termes === "") {
        return false;
      }

    }
    return true;
  }

  chooseCourse = (myCourse: course | null) => {
    let conf: boolean = false;
    if (this.props.prepareExam.questions.length !== 0) {
      conf = window.confirm("Are you sure do you want to change course? You will loose your selected questions")
    }
    if (conf === true) {
      for (let x of this.props.prepareExam.questions) {
        this.props.addExamQuestion(x);
      }
      this.props.setSelectedCourse(myCourse);
      this.setState({ currentStep: PrepareExamStepsEnum.Step1 });
    }
    if (this.props.prepareExam.questions.length === 0) {
      this.props.setSelectedCourse(myCourse);
    }
    this.setState({ selectCourse: false });
    if (myCourse !== null) {
      this.props.loadQuestionBank(myCourse.course_id, this.setLoading);
    }
  }

  examQuestionsIds = (questions: QuestionInterface[]): string[] => {
    let ids: string[] = [];
    for (let question of questions) {
      ids = [...ids, question.id];
    }
    return ids;
  }

  submitExamPreparation = async () => {
    this.setState({ formErr: null });
    if (this.props.periods.selected_period === null || this.props.periods.selected_period.is_active === false) {
      return this.setState({
        formErr: {
          element: "period",
          msg: "Please select valid period!",
        },
        selectPeriod: true,
      })
    }
    if (this.props.course.selected_course === null) {
      return this.setState({
        formErr: {
          element: "course",
          msg: "Please select a course!",
        },
        selectCourse: true,
      })
    }
    if (this.state.exam_date === "") {
      return this.setState({
        formErr: {
          element: "exam_date",
          msg: "Please choose exam date",
        }
      })
    }
    if (this.state.start_time === "") {
      return this.setState({
        formErr: {
          element: "start_time",
          msg: "Please fill exam start time",
        }
      })
    }
    if (this.state.end_time === "") {
      return this.setState({
        formErr: {
          element: "end_time",
          msg: "Please fill exam ending time",
        }
      })
    }
    if (this.state.duration_hours === 0 && this.state.duration_minutes === 0) {
      return this.setState({
        formErr: {
          element: "duration_hours",
          msg: "Please fill exam duration",
        }
      })
    }
    if (this.state.questions_per_page < 1) {
      return this.setState({
        formErr: {
          element: "questions_per_page",
          msg: "Please fill number of questions per page",
        }
      })
    }
    if (this.state.number_format === "") {
      return this.setState({
        formErr: {
          element: "number_format",
          msg: "Please fill questions numbering format",
        }
      })
    }
    if (this.state.discription === "") {
      return this.setState({
        formErr: {
          element: "discription",
          msg: "Please fill exam description",
        }
      })
    }
    if (this.props.prepareExam.questions.length === 0) {
      return this.setState({
        formErr: {
          element: "questions",
          msg: "Please choose questions",
        }
      })
    }
    if (this.state.termes === "") {
      return this.setState({
        formErr: {
          element: "termes",
          msg: "Please fill exam terms and conditions",
        }
      })
    }
    this.setState({ loading: true });
    try {
      await axios.post(`${API.QBank}/exam`, {
        period: this.props.periods.selected_period.application_session_id,
        course: this.props.course.selected_course.course_id,
        questions: this.examQuestionsIds(this.props.prepareExam.questions),
        exam_status: PrepareExamStatus.Created,
        termes: this.state.termes,
        questions_per_page: this.state.questions_per_page,
        exam_date: this.state.exam_date,
        start_time: this.state.start_time,
        end_time: this.state.end_time,
        duration: this.state.duration_minutes + (this.state.duration_hours*60),
        number_format: this.state.number_format,
        discription: this.state.discription,
      });
      for (let x of this.props.prepareExam.questions) {
        this.props.addExamQuestion(x);
      }
      this.props.setSelectedCourse(null);
      this.setState({ redirectToExams: true });
      this.props.PushPopupAlert({
        title: "Success",
        description: "Exam has prepared successfully!",
        theme: PopupAlertTheme.success
      })
    } catch (error: any) {
      if (error.response) {
        if (error.response.data) {
          if (error.response.data.errors !== undefined) {
            if (error.response.data.errors.length > 0) {
              this.setState({formErr: {element: "main", msg: error.response.data.errors[0].message}});
            } else {
              this.setState({formErr: {element: "main", msg: "Failed to update Question!"}});
            }
          } else {
            this.setState({formErr: {element: "main", msg: "Failed to update Question!"}});
          }
        } else {
          this.setState({formErr: {element: "main", msg: "Failed to update Question!"}});
        }
      }
      console.log("Exam err: ", {...error})
    }
    this.setState({ loading: false });
  }

  componentDidMount = () => {
    if (this.props.questionTypes.questionTypes === null || this.props.questionTypes.questionTypes.length === 0) {
      this.props.loadQuestionTypes(this.setLoading);
    }
    if (this.props.periods.applicationPeriods === null || this.props.periods.applicationPeriods.length === 0) {
      this.props.loadApplicationPeriods(this.setLoading);
    }
    if (this.props.course.selected_course === null) {
      this.setState({ selectCourse: true });
    }
    if (this.props.course.selected_course !== null) {
      this.props.loadQuestionBank(this.props.course.selected_course.course_id, this.setLoading);
    }
  }
  unSelectQuestion = () => {
    this.setState({ editQuestion: null });
  }
  findTotalMarks = (questions: QuestionInterface[]) => {
    let total = 0;
    for (let question of questions) {
      total = total + question.marks;
    }
    return total;
  }
  handleChangeTermes = (value: string) => {
    console.log("Termes: ", value);
    this.setState({
      termes: value,
      formErr: null,
    })
  }
  setSelectCourse = (status: boolean) => {
    this.setState({ selectCourse: status })
  }
  setSelectPeriod = (status: boolean) => {
    this.setState({ selectPeriod: status })
  }
  render() {
    if (this.state.redirectToExams === true) {
      return <Redirect to="exams-list" />
    }
    return (
      <Fragment>
      {this.state.editQuestion === null ?
      <div>
        <PrepareExamSteps 
              currentStep={this.state.currentStep}
              setStep={this.setStep} 
              course={this.props.course} 
              periods={this.props.periods} 
              setSelectCourse={this.setSelectCourse} 
              setSelectPeriod={this.setSelectPeriod}
              stepStatus={this.StepStatus} />
        {this.state.loading === true ? 
        <div className="px-4 py-6 mt-4 bg-white rounded">
          <div className="text-gray-600 mb-3 px-4">Performing action, Please wait...</div>
          <Loading /> 
        </div>:
        <div className="rounded bg-white px-3 py-3 mt-2 mb-28">
          {this.state.currentStep === PrepareExamStepsEnum.Step2 &&
          <div className="grid md:grid-cols-12 gap-4">
            <div className="md:col-span-6 flex flex-col">
              <span className="text-gray-700 font-semibold">Exam date</span>
              <input 
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                this.setState({
                  exam_date: e.target.value,
                  formErr: null,
                })
              }}
              value={this.state.exam_date}
              type="date" className={`input-white h-10 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "exam_date" ? "border-2 border-red-600" : "border"}`} />
              {this.state.formErr !== null && this.state.formErr.element === "exam_date" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-3 flex flex-col">
              <span className="text-gray-700 font-semibold">Duration(Hours)</span>
              <select 
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                this.setState({
                  duration_hours: parseInt(e.target.value),
                  formErr: null,
                })
              }}
              value={this.state.duration_hours}
              className={`input-white h-10 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "duration_hours" ? "border-2 border-red-600" : "border"}`}>
              {getHoursList().map((item, i) => (
                <option key={i + 1}>{item}</option>
              ))}
              </select>
              {this.state.formErr !== null && this.state.formErr.element === "duration_hours" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-3 flex flex-col">
              <span className="text-gray-700 font-semibold">Duration(Minutes)</span>
              <select 
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                this.setState({
                  duration_minutes: parseInt(e.target.value),
                  formErr: null,
                })
              }}
              value={this.state.duration_minutes}
              className={`input-white h-10 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "duration_minutes" ? "border-2 border-red-600" : "border"}`}>
              {getMinutesList().map((item, i) => (
                <option key={i + 1}>{item}</option>
              ))}
              </select>
              {this.state.formErr !== null && this.state.formErr.element === "duration_minutes" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-6 flex flex-col">
              <span className="text-gray-700 font-semibold">Exam start time</span>
              <input 
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                this.setState({
                  start_time: e.target.value,
                  formErr: null,
                })
              }}
              value={this.state.start_time}
              type="time" className={`input-white h-10 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "start_time" ? "border-2 border-red-600" : "border"}`} />
              {this.state.formErr !== null && this.state.formErr.element === "start_time" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-6 flex flex-col">
              <span className="text-gray-700 font-semibold">Exam ending time</span>
              <input 
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                this.setState({
                  end_time: e.target.value,
                  formErr: null,
                })
              }}
              value={this.state.end_time}
              type="time" className={`input-white h-10 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "end_time" ? "border-2 border-red-600" : "border"}`} />
              {this.state.formErr !== null && this.state.formErr.element === "end_time" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-6 flex flex-col">
              <span className="text-gray-500 font-semibold">Number of questions per page</span>
              <input 
              disabled={true}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                this.setState({
                  questions_per_page: parseInt(e.target.value),
                  formErr: null,
                })
              }}
              value={this.state.questions_per_page}
              type="number" className={`rounded px-3 py-2 h-10 border-gray-200 bg-gray-100 ${this.state.formErr !== null && this.state.formErr.element === "questions_per_page" ? "border-2 border-red-600" : "border"}`} />
              {this.state.formErr !== null && this.state.formErr.element === "questions_per_page" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-6 flex flex-col">
              <span className="text-gray-500 font-semibold">Questions number format: {this.state.number_format}</span>
              <select
                onChange={(e) => {
                  this.setState({
                    number_format: e.target.value,
                    formErr: null,
                  })
                }}
                disabled={true}
                className={`px-3 py-2 rounded bg-gray-100 h-10 border-gray-200 ${this.state.formErr !== null && this.state.formErr.element === "number_format" ? "border-2 border-red-600" : "border"}`}>
                <option value="">Select Numbering format</option>
                {EXAM_QUESTIONS_NUMBERING.map((item, i) => (
                  <option selected={item.key === this.state.number_format ? true : false} key={i + 1} value={item.key}>{item.value}</option>
                ))}
              </select>
              {this.state.formErr !== null && this.state.formErr.element === "number_format" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-12 flex flex-col">
              <span className="text-gray-700 font-semibold">Exam description</span>
              <textarea
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                this.setState({
                  discription: e.target.value,
                  formErr: null,
                })
              }}
              value={this.state.discription}
              className={`input-white min-h-8 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "discription" ? "border-2 border-red-600" : "border"}`}></textarea>
              {this.state.formErr !== null && this.state.formErr.element === "discription" && <div className="text-red-600 text-xs font-bold italic">{this.state.formErr.msg}</div>}
            </div>
            <div className="md:col-span-12 flex flex-col">
              {/* <div dangerouslySetInnerHTML={{__html: this.state.termes}}></div> */}
              <span className="text-gray-700 font-semibold">Exam terms and conditions</span>
              {/* <textarea 
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => {
                this.setState({
                  termes: e.target.value,
                  formErr: null,
                })
              }}
              value={this.state.termes}
              className={`input-white min-h-8 border-gray-400 ${this.state.formErr !== null && this.state.formErr.element === "termes" ? "border-2 border-red-600" : "border"}`}></textarea> */}
              <ReactQuill value={this.state.termes}
                  className="rounded mb-10"
                  onChange={this.handleChangeTermes} />
              {this.state.formErr !== null && this.state.formErr.element === "termes" && <div className="text-red-600 text-xs font-bold italic mt-2">{this.state.formErr.msg}</div>}
            </div>
          </div>}
          {this.state.currentStep === PrepareExamStepsEnum.Step1 &&
          <div className="border rounded px-2 py-2 mt-4">
            <div className="flex flex-row items-center justify-between">
              <span className="font-bold text-blue-600 text-lg">Question paper</span>
              <div className="flex flex-row gap-2">
                <span className="rounded-full bg-yellow-50 text-yellow-800 font-bold px-4 py-2 text-sm underline h-max">Total marks: {this.findTotalMarks(this.props.prepareExam.questions)}</span>
                <div onClick={() => this.props.course.selected_course === null ? this.setState({ selectCourse: true }) : this.setState({ addQuestionModal: true })} className="btn bg-blue-50 text-blue-600 hover:bg-blue-100 cursor-pointer flex flex-row items-center"><MdAdd className="text-2xl" /> Add question</div>
              </div>
            </div>
            <div className="flex flex-col mt-2">
              {this.props.prepareExam.questions.length === 0 ?
              <div className={`${this.state.formErr !== null && this.state.formErr.element === "questions" ? "bg-red-50 border-2 border-red-600 text-red-600" : "bg-gray-100"} rounded mt-2`}>
                <NoResult theme={NoResultThemes.Dark} value={"No questions added so far!"} />
              </div> :
              this.props.questionTypes && this.props.prepareExam.questions.map((item, i) => (
                <QuestionItem 
                key={i + 1} 
                question={item} 
                callBack={this.removeToQuestionPaper} 
                status={true} 
                questionTypes={this.props.questionTypes}
                select={false}
                number={i + 1}
                questionsList={this.props.prepareExam.questions} />
              ))}
            </div>
          </div>}
          <div className="my-4" onClick={() => this.setState({ formErr: null })}>
            {this.state.formErr !== null && this.state.formErr.element !== "" && 
              <Alert title={"Missing info"} description={this.state.formErr.msg} theme={AlertInterface.danger} />
            }
          </div>
          <div className="flex flex-row items-center justify-between mt-4">
            {this.state.currentStep === PrepareExamStepsEnum.Step1 ?
            <div className="btn border cursor-pointer">Cancel</div> :
            <div onClick={() => this.setStep(PrepareExamStepsEnum.Step1)} className="btn border cursor-pointer">Back to questions</div>}
            {this.state.currentStep === PrepareExamStepsEnum.Step1 ?
            <div className="flex flex-row items-center">
              <div onClick={() => this.setStep(PrepareExamStepsEnum.Step2)} className="btn bg-blue-600 text-white cursor-pointer">Continue</div>
            </div>:
            <div className="flex flex-row items-center">
              <div onClick={() => this.submitExamPreparation()} className="btn bg-blue-600 text-white cursor-pointer">Submit exam</div>
            </div>}
          </div>
        </div>}
      </div> :
      this.props.questionBank.questions === null ? <Loading /> :
      this.props.questionBank.questions.length === 0 ? <div>No Questions available!</div> :
      <EditQuestion 
        selectedQuestion={this.state.editQuestion} 
        questionsList={this.props.questionBank.questions}
        unSelectQuestion={this.unSelectQuestion} 
      />}
      {this.state.selectCourse === true &&
      <Modal
        title=""
        backDrop={true}
        theme={Themes.default}
        close={() => this.setState({ selectCourse: false })}
        backDropClose={true}
        widthSizeClass={ModalSize.large}
        displayClose={true}
        padding={{title: true,}}>
        <div className="-mt-6">
          <Courses setSelectedCourse={this.chooseCourse} selected_course_id={this.props.course.selected_course && this.props.course.selected_course.course_id} />
        </div>
      </Modal>}
      {this.state.addQuestionModal === true &&
      <Modal
        title="Add exam questions"
        backDrop={true}
        theme={Themes.default}
        close={() => this.setState({ addQuestionModal: false })}
        backDropClose={true}
        widthSizeClass={ModalSize.extraLarge}
        displayClose={true}
        padding={{title: true,}}>
        {this.props.course.selected_course === null ? <div>Please choose a course</div> :
        this.props.questionBank.questions === null ? <Loading /> :
        <Questions 
        selectedCourse={this.props.course.selected_course} 
        callBack={this.addQuestionToPaper} 
        questionsList={this.props.questionBank.questions}
        questionPaper={this.props.prepareExam.questions} />}
      </Modal>}
      {this.state.selectPeriod === true &&
      <Modal
        title="Choose a period"
        backDrop={true}
        theme={Themes.default}
        close={() => this.setState({ selectPeriod: false })}
        backDropClose={true}
        widthSizeClass={ModalSize.large}
        displayClose={true}
        padding={{title: true,}}>
          <div className="px-2">
            <ApplicationPeriods 
            applicationPeriods={this.props.periods.applicationPeriods} 
            setSelectedPeriod={this.props.setSelectedPeriod} 
            showPeriodFilter={this.showPeriodFilter} />
          </div>
      </Modal>}
      </Fragment>
    );
  }
}


const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  questionTypes,
  prepareExam,
  periods,
  system,
}: StoreState): { auth: Auth; course: courseStoreInterface; schoolLevels: schoolLevelsStoreInterface; questionBank: QuestionBankStoreInterface; questionTypes: questionTypesStoreInterface; prepareExam: PrepareExamsStoreInterface; periods: applicationPeriodsStoreInterface; system: System } => {
  return { auth: auth, course: course, schoolLevels: schoolLevels, questionBank: questionBank, questionTypes: questionTypes, prepareExam: prepareExam, periods: periods, system: system };
};

export const PrepareExam = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  setUploadedQuestions,
  loadQuestionTypes,
  loadQuestionBank,
  setSelectedCourse,
  addExamQuestion,
  loadApplicationPeriods,
  setSelectedPeriod,
  PushPopupAlert,
})(_PrepareExam);
