import { candidateExamStoreInterface } from "../actions/candidate_exam";
import { applicationExamAPI, ApplicationPeriodGet } from "../interfaces/application";
import { ExamResultsDetails } from "../interfaces/exam";
import { PositionAPI, PositionAPICourse } from "../interfaces/position";
import { questionType, questionTypeElements, questionTypeElementsType } from "../interfaces/question";

export const getQuestionType = (question_types: questionType[] | null, type_id: string) => {
  let type: questionTypeElementsType | null = null;
  if (question_types !== null) {
    let check = question_types.find(itm => itm.id === type_id);
    if (check !== undefined) {
      let check_type = questionTypeElements.find(itm => check && itm.key === check.element);
      if (check_type !== undefined) {
        type = check_type.key
      }
    }
  }
  return type;
}

export const findTotalMarks = (courses: PositionAPICourse[], results: ExamResultsDetails[] | null) => {
  if (results !== null) {
    let sum: number = 0;
    for (let course of courses) {
      let test = results.find(itm => itm.course === course.course_id);
      if (test !== undefined) {
        sum = sum + test.user_total_marks;
      }
    }
    return sum.toFixed(2)
  } else {
    return "Wait..."
  }
}

export const CheckIfExamDone = (courses: PositionAPICourse[], results: ExamResultsDetails[] | null) => {
  let res: boolean = false;
  if (results !== null) {
    for (let course of courses) {
      let test = results.find(itm => itm.course === course.course_id);
      if (test !== undefined) {
        res = true;
      }
    }
  }
  return res;
}

export const validateCalculatedFormula = (formula: string): { status: boolean; msg: string; } => {
  let characters: string[] = [];
  if (formula === undefined || formula === "" || formula.length === undefined || formula.length === 0) {
    return {
      status: false,
      msg: `Please type a formula`
    }
  }
  if (formula.length < 3) {
    return {
      status: false,
      msg: `The formula in not valid, it is not complete!`
    }
  }
  for (let el of formula) {
    characters = [...characters, el];
  }
  if (characters[0] !== `"`) {
    return {
      status: false,
      msg: `Formula should start with (") character`
    }
  }
  if (characters.filter(itm => itm === `"`).length < 2) {
    return {
      status: false,
      msg: `Formula is missing closing (") character`
    }
  }
  if (characters.filter(itm => itm === `"`).length > 2) {
    return {
      status: false,
      msg: `Formula is invalid, because it has many (") characters (${characters.filter(itm => itm === `"`).length})`
    }
  }
  if (characters[characters.length-1] !== `"` && characters.filter(itm => itm === `"`).length === 2) {
    let nextCharacter: string = "";
    for (let i=0; i < characters.length; i++) {
      if (characters[i] === `"` && i !== 0) {
        nextCharacter = characters[i+1];
      }
    }
    if (nextCharacter !== `;`) {
      return {
        status: false,
        msg: `Formula is missing separation character (;) between formula and error`
      }
    }
    if (characters[characters.length-1] === `;`) {
      return {
        status: false,
        msg: `Formula is missing error after (;) character`
      }
    }
  }
  return {
    status: true,
    msg: `Formula is valid`
  }
}

export function convertFromStringToDate(responseDate: string) {
  let dateComponents = responseDate.split('T');
  let datePieces = dateComponents[0].split("-");
  let timePieces = dateComponents[1].split(":");
  return(new Date(parseInt(datePieces[2]), (parseInt(datePieces[1]) - 1), parseInt(datePieces[0]),
                       parseInt(timePieces[0]), parseInt(timePieces[1]), parseInt(timePieces[2])))
}

export enum ExamTimeStatus {
  Waiting = "Waiting",
  Online = "Online",
  Expired = "Expired",
}

export const validateExamDate = (exam_date: string, start_time: string, end_time: string) => {
  let status: ExamTimeStatus = ExamTimeStatus.Online;
  // Dates
  const toDay: Date = new Date();
  const examDate = new Date(exam_date);

  // Time
  const startTime = convertFromStringToDate(examDate.getDate() + "-" + (examDate.getMonth() + 1) + "-" + examDate.getFullYear() + "T" + start_time + ":0");
  const endTime = convertFromStringToDate(examDate.getDate() + "-" + (examDate.getMonth() + 1) + "-" + examDate.getFullYear() + "T" + end_time + ":0");

  // check exam date
  if (
    examDate.getDate() < toDay.getDate() ||
    examDate.getMonth() < toDay.getMonth() ||
    examDate.getFullYear() < toDay.getFullYear()
  ) {
    status = ExamTimeStatus.Waiting;
  }

  if (
    examDate.getDate() > toDay.getDate() ||
    examDate.getMonth() > toDay.getMonth() ||
    examDate.getFullYear() > toDay.getFullYear()
  ) {
    status = ExamTimeStatus.Expired;
  }

  if (
    examDate.getDate() === toDay.getDate() &&
    examDate.getMonth() === toDay.getMonth() &&
    examDate.getFullYear() === toDay.getFullYear()
  ) {
    if (startTime.getTime() > toDay.getTime()) {
      status = ExamTimeStatus.Waiting;
    } else if (startTime.getTime() <= toDay.getTime() && endTime.getTime() >= toDay.getTime()) {
      status = ExamTimeStatus.Online;
    } else {
      status = ExamTimeStatus.Expired;
    }
  }
  console.log("Exam Status: ", status);
  return status;
}


export const InitializeExamsDone = (
  selected_period: ApplicationPeriodGet | null,
  selectedPosition: PositionAPI | null,
  candidateExam: candidateExamStoreInterface,
  loadExamsDone: (periodId: string, userId: string, callback: (loading: boolean) => void) => void,
  user_id: string,
  setLoadingExamsDone: (status: boolean) => void,
  selectedExams: applicationExamAPI[] | null,
  InitializeExamDetails: (periodId: string, courseId: string, district_id: string, center_id: string, room_id: string, position_id: string, callback: (loading: boolean) => void) => void,
  setLoadingExams: (status: boolean) => void
) => {
  if (selected_period !== null && selectedPosition !== null) {
    loadExamsDone(selected_period.application_session_id, user_id, setLoadingExamsDone);
    for(let course of selectedPosition.courses) {
      let testDone = candidateExam.user_exams_temp.find(itm => itm.course === course.course_id);
      if (testDone === undefined) {
        let center_id = selectedExams?.find(itm => itm.course_id === course.course_id);
        if (center_id !== undefined) {
          InitializeExamDetails(
            selected_period.application_session_id,
            course.course_id,
            center_id.district_id, 
            center_id.center_id,
            center_id.room_id,
            selectedPosition.position_id,
            setLoadingExams
          )
        }
      }
    }
  }
}

export const getActiveExam = (candidateExam: candidateExamStoreInterface): ExamResultsDetails | null => {
  if (candidateExam.user_exams_done.length > 0) {
    for (let exam of candidateExam.user_exams_done) {
      if (validateExamDate(exam.exam_date, exam.start_time, exam.end_time) === ExamTimeStatus.Online) {
        return exam;
      }
    }
  }
  return null;
}